"use client"

import { useEffect, useRef, useState } from 'react'
import { useGame } from '@/contexts/GameContext'

interface Position {
  x: number
  y: number
}

interface Plant {
  x: number
  y: number
  stage: number
  type: string
}

interface GameWorldProps {
  onMove?: (position: Position) => void
}

export default function GameWorld({ onMove }: GameWorldProps) {
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const [playerPos, setPlayerPos] = useState<Position>({ x: 400, y: 300 })
  const [isMoving, setIsMoving] = useState(false)
  const [direction, setDirection] = useState<string>('')
  const [plants, setPlants] = useState<Plant[]>([])
  const [selectedTool, setSelectedTool] = useState<string>('none')
  const { gameState, updateEnergy } = useGame()

  // Game loop
  useEffect(() => {
    const canvas = canvasRef.current
    if (!canvas) return

    const ctx = canvas.getContext('2d')
    if (!ctx) return

    // Set canvas size
    canvas.width = 800
    canvas.height = 600

    // Game loop
    let animationFrameId: number

    const render = () => {
      // Clear canvas
      ctx.clearRect(0, 0, canvas.width, canvas.height)

      // Draw background (grass pattern)
      ctx.fillStyle = '#2d5a27'
      ctx.fillRect(0, 0, canvas.width, canvas.height)

      // Draw grid pattern
      ctx.strokeStyle = '#234d1f'
      ctx.lineWidth = 1
      const gridSize = 32

      for (let x = 0; x < canvas.width; x += gridSize) {
        ctx.beginPath()
        ctx.moveTo(x, 0)
        ctx.lineTo(x, canvas.height)
        ctx.stroke()
      }

      for (let y = 0; y < canvas.height; y += gridSize) {
        ctx.beginPath()
        ctx.moveTo(0, y)
        ctx.lineTo(canvas.width, y)
        ctx.stroke()
      }

      // Draw plants
      plants.forEach(plant => {
        ctx.fillStyle = '#8B4513' // soil color
        ctx.fillRect(plant.x - 12, plant.y - 12, 24, 24)

        if (plant.stage > 0) {
          // Plant growth visualization
          ctx.fillStyle = '#90EE90'
          const height = (plant.stage / 4) * 20 // Max height is 20 pixels
          ctx.fillRect(plant.x - 4, plant.y - height, 8, height)
        }
      })

      // Draw player
      ctx.fillStyle = '#FFFFFF'
      ctx.beginPath()
      ctx.arc(playerPos.x, playerPos.y, 16, 0, Math.PI * 2)
      ctx.fill()

      // Draw player name and selected tool
      ctx.fillStyle = '#FFFFFF'
      ctx.font = 'bold 16px Arial'
      ctx.textAlign = 'center'
      ctx.fillText('Player', playerPos.x, playerPos.y - 30)
      if (selectedTool !== 'none') {
        ctx.fillText(selectedTool, playerPos.x, playerPos.y - 50)
      }

      // Request next frame
      animationFrameId = requestAnimationFrame(render)
    }

    render()

    return () => {
      cancelAnimationFrame(animationFrameId)
    }
  }, [playerPos, plants, selectedTool])

  // Handle keyboard input
  useEffect(() => {
    const moveSpeed = 4

    const handleKeyDown = (e: KeyboardEvent) => {
      setIsMoving(true)
      switch (e.key) {
        case 'ArrowUp':
        case 'w':
          setDirection('up')
          setPlayerPos(prev => ({
            ...prev,
            y: Math.max(16, prev.y - moveSpeed)
          }))
          break
        case 'ArrowDown':
        case 's':
          setDirection('down')
          setPlayerPos(prev => ({
            ...prev,
            y: Math.min(584, prev.y + moveSpeed)
          }))
          break
        case 'ArrowLeft':
        case 'a':
          setDirection('left')
          setPlayerPos(prev => ({
            ...prev,
            x: Math.max(16, prev.x - moveSpeed)
          }))
          break
        case 'ArrowRight':
        case 'd':
          setDirection('right')
          setPlayerPos(prev => ({
            ...prev,
            x: Math.min(784, prev.x + moveSpeed)
          }))
          break
        case ' ': // Spacebar to plant
          if (gameState.player.energy >= 5) {
            const newPlant: Plant = {
              x: playerPos.x,
              y: playerPos.y,
              stage: 0,
              type: 'carrot'
            }
            setPlants(prev => [...prev, newPlant])
            updateEnergy(-5)
          }
          break
        case '1':
          setSelectedTool('Hoe')
          break
        case '2':
          setSelectedTool('Seeds')
          break
        case '3':
          setSelectedTool('Watering Can')
          break
      }
    }

    const handleKeyUp = () => {
      setIsMoving(false)
      setDirection('')
    }

    window.addEventListener('keydown', handleKeyDown)
    window.addEventListener('keyup', handleKeyUp)

    return () => {
      window.removeEventListener('keydown', handleKeyDown)
      window.removeEventListener('keyup', handleKeyUp)
    }
  }, [onMove, gameState.player.energy, updateEnergy, playerPos])

  // Plant growth system
  useEffect(() => {
    const growthInterval = setInterval(() => {
      setPlants(prevPlants =>
        prevPlants.map(plant => ({
          ...plant,
          stage: plant.stage < 4 ? plant.stage + 1 : plant.stage
        }))
      )
    }, 10000) // Plants grow every 10 seconds

    return () => clearInterval(growthInterval)
  }, [])

  return (
    <div className="relative w-full h-full flex items-center justify-center bg-gray-900">
      <div className="absolute top-4 left-4 text-white text-lg z-10 bg-gray-800/80 p-4 rounded-lg">
        <p className="font-bold mb-2">Controls:</p>
        <p>WASD / Arrows - Move</p>
        <p>Space - Plant (5 energy)</p>
        <p>1-3 - Select Tool</p>
      </div>
      
      <canvas
        ref={canvasRef}
        className="border-4 border-gray-700 rounded-lg shadow-lg"
        style={{ imageRendering: 'pixelated' }}
      />
      
      {/* Mobile Controls */}
      <div className="absolute bottom-4 right-4 md:hidden grid grid-cols-3 gap-2">
        <button
          className="p-4 bg-white/20 rounded-lg active:bg-white/30 text-white font-bold text-2xl"
          onTouchStart={() => setDirection('up')}
          onTouchEnd={() => setDirection('')}
        >
          ‚Üë
        </button>
        <button
          className="p-4 bg-white/20 rounded-lg active:bg-white/30 text-white font-bold text-2xl"
          onTouchStart={() => setDirection('down')}
          onTouchEnd={() => setDirection('')}
        >
          ‚Üì
        </button>
        <button
          className="p-4 bg-white/20 rounded-lg active:bg-white/30 text-white font-bold text-2xl"
          onTouchStart={() => setDirection('left')}
          onTouchEnd={() => setDirection('')}
        >
          ‚Üê
        </button>
        <button
          className="p-4 bg-white/20 rounded-lg active:bg-white/30 text-white font-bold text-2xl"
          onTouchStart={() => setDirection('right')}
          onTouchEnd={() => setDirection('')}
        >
          ‚Üí
        </button>
      </div>
    </div>
  )
}
