"use client"

import * as React from "react"
import * as ProgressPrimitive from "@radix-ui/react-progress"

import { cn } from "@/lib/utils"

type ProgressProps = React.ComponentProps<typeof ProgressPrimitive.Root> & {
  variant?: "level" | "energy" | "experience"
}

function Progress({
  className,
  value,
  variant = "experience",
  ...props
}: ProgressProps) {
  const indicatorClass = {
    level: "bg-gradient-to-r from-yellow-400 to-yellow-500",
    energy: "bg-gradient-to-r from-green-400 to-green-500",
    experience: "bg-gradient-to-r from-indigo-500 to-indigo-600",
  }[variant]

  return (
    <ProgressPrimitive.Root
      data-slot="progress"
      className={cn(
        "relative h-4 w-full overflow-hidden rounded-full bg-gray-900/50 border-2 border-gray-600",
        className
      )}
      {...props}
    >
      <ProgressPrimitive.Indicator
        data-slot="progress-indicator"
        className={cn("h-full w-full flex-1 transition-all duration-300", indicatorClass)}
        style={{ transform: `translateX(-${100 - (value || 0)}%)` }}
      />
    </ProgressPrimitive.Root>
  )
}

export { Progress }
